footer{
    background-color: var(--nav-gray);
    padding: var(--default-padding);   
    display: flex;
    justify-content: space-between; 
}

footer i{
    font-size: 30px;
    margin: 0 5px;

}

footer div{
    max-width: 200px;
}

footer .separator{
    background-color: rgb(82, 76, 76);
    width: 2px;
    height: 100%;
}

/*Mobile style*/

@media only screen and (max-width: 500px) {
    footer{
        flex-direction: column;
        padding: 10px;
    }

    footer .separator{
        width: 100%;
        height: 2px;
        margin: 10px;
    }
    
    footer div{
        max-width: 100%;
        text-align: center;
    }
}