.image-slider-container{
    position: relative;
    min-height: 600px;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 50px 100px;
}

.image-slider-container img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    position: absolute;
    opacity: 0;
    transition: all 1s;
    filter: grayscale(1);
}

.image-slider-container .visible{
    opacity: 0.3;
}

.image-slider-container h1{
    font-size: 128px;
    color: var(--title-yellow);
    position: relative;
    font-weight: 400;
    text-align: center;
}

.image-slider-container h2{
    font-size: 24px;
    position: relative;
    font-weight: 200;
    text-align: center;
}

/* mobile */

@media only screen and (max-width: 500px) {
    .image-slider-container{
        padding: 30px 10px;
    }

    .image-slider-container h2{
        font-size: 26px;
    }
}











